import Mock from 'mockjs'

const data = []
const institution = {
  groupName: '机构分组',
  asc: true,
  lists:[]
}
const department = {
  groupName: '部门分组',
  asc: true,
  lists:[]
}

for (let i = 0; i < 10; i++) {
  institution.lists.push(Mock.mock({
    id: Mock.Random.integer(10000, 99999),
    name: Mock.Random.cname(2, 12),
    profit: Mock.Random.float(60, 100, 1, 2 ),
    area: Mock.Random.region()
  }))
}
for (let i = 0; i < 10; i++) {
  department.lists.push(Mock.mock({
    id: Mock.Random.integer(10000, 99999),
    name: Mock.Random.cname(2, 12),
    profit: Mock.Random.float(60, 100, 1, 2 ),
    area: Mock.Random.region()
  }))
}
data.push(institution)
data.push(department)
export default Mock.mock('http://helloworld.cn', {
  data 
})


