<template>
  <el-row>
    <el-col :span="24" class="wrap-main" @keyup.enter.native="keyUp('ruleForm')">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
        <el-form-item label="用户名" prop="userName">
          <el-input v-model="ruleForm.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="passWord">
          <el-input type="password" v-model="ruleForm.passWord"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="passWordConfirm">
          <el-input type="password" v-model="ruleForm.passWordConfirm"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input type="email" v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="电话号码" prop="phoneNumber">
          <el-input v-model="ruleForm.phoneNumber"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>
<script>
export default {
  props: {
    ruleForm: Object
  },
  data () {
    const validateUserName = (rule, value, callback) => {
      let userNameLimited = /^[A-Za-z0-9]+$/
      if (!userNameLimited.test(value)) {
        callback(new Error('用户名格式不对，不能包含特殊字符'))
      } else {
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (value !== this.ruleForm.passWord) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    const validatePhoneNumber = (rule, value, callback) => {
      let phoneNumberLimited = /^1[34578]\d{9}$/
      if (!phoneNumberLimited.test(value)) {
        callback(new Error('请输入中国国内的手机号!'))
      } else {
        callback()
      }
    }
    return {
      rules: {
        userName: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 5, max: 15, message: '长度在 6 到 15 个字符' },
          { validator: validateUserName }
        ],
        passWord: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min:6, max: 15, message: '长度在 5 到 15 个字符' }
        ],
        passWordConfirm: [
          { required: true, message: '请输入确认密码', trigger: 'blur' },
          { validator: validatePass }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '邮箱格式不正确' }          
        ],
        phoneNumber: [
          { required: true, message: '请输入电话号码', trigger: 'blur' },
          { validator: validatePhoneNumber }
        ]
      }
    }
  },
  methods: {
    submitForm (name) {
      console.log(name)
      this.$confirm('确认提交？')
        .then(_ => {
          this.$refs[name].resetFields()
          this.$emit('submit')
          done();
        })
        .catch(_ => {});
    },
    resetForm (name) {
      this.$refs[name].resetFields()      
    },
    keyUp (name) {
      this.$confirm('确认提交？')
        .then(_ => {
          this.$refs[name].resetFields()
          this.$emit('submit')
          done();
        })
        .catch(_ => {});
    }
  }
}
</script>
<style scoped>
  .el-form {
    width: 400px;
  }
</style>





