<template>
  <el-row>
    <el-input
      class="search"
      placeholder="请选择机构"
      icon="search"
      v-model="input"
      :on-icon-click="handleIconClick">
    </el-input>
    <el-dialog class="box" title="机构选择" v-model="addFormVisible" :close-on-click-modal="false">
      <el-input class="search" placeholder="请选择机构" icon="search"></el-input>
      <el-tree class="tree" :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
    </el-dialog>
  </el-row>
</template>

<script>
export default {
  data() {
    return {
      input: '',
      addFormVisible: false,
      data: [{
        label: '一级 1',
        children: [{
          label: '二级 1-1',
          children: [{
            label: '三级 1-1-1'
          }]
        }]
      }, {
        label: '一级 2',
        children: [{
          label: '二级 2-1',
          children: [{
            label: '三级 2-1-1'
          }]
        }, {
          label: '二级 2-2',
          children: [{
            label: '三级 2-2-1'
          }]
        }]
      }, {
        label: '一级 3',
        children: [{
          label: '二级 3-1',
          children: [{
            label: '三级 3-1-1'
          }]
        }, {
          label: '二级 3-2',
          children: [{
            label: '三级 3-2-1'
          }]
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    }
  },
  methods: {
    handleIconClick(ev) {
      console.log(ev);
      this.addFormVisible = true
    },
    handleNodeClick(data) {
      console.log(data);
    }
  },
  mounted: function () {
    this.$http({url: '/demoWar/login',
                method: 'POST',
                emulateJSON: true,
                data: "username=admin&&password=password"
    }).then(
      (res) => {
        console.log(res)
      },
      (fileData) => {
        console.log(fileData)    
      }
    )
  }
}
</script>
<style scoped>
  .search {
    width: 120px;
  }
  .box {
    text-align: left;
  }
  .tree {
    margin-top: 10px;
    height: 200px;
    overflow: auto;
  }
</style>



