import Vue from 'vue'
import Router from 'vue-router'

import home from '@/components/page.home.vue'
import form from '@/components/form.vue'
import institutionPerformance from '@/components/institutionPerformance.vue'
import formConfirm from '@/components/formConfirm.vue'
import test from '@/components/test.vue'
import selfTable from '@/components/selfTable.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: home,
      children: [
        {
          path:'/home/institutionPerformance',
          component:institutionPerformance,
          name:'机构业绩统计'
        },
        {
          path:'/home/formConfirm',
          component:formConfirm,
          name:'formConfirm'
        },
        {
          path:'/home/form3',
          component:form,
          name:'客户经理定级统计'
        },
        {
          path:'/home/selfTable',
          component:selfTable,
          name:'selfTable'
        },
        {
          path:'/home/form5',
          component:form,
          name:'分行部门得分统计'
        },
        {
          path:'/home/test',
          component:test,
          name:'test'
        }
      ]   
    }
  ]
})



