<template>
  <el-row class="wrap">
    <!--<div class="concat" v-show="isConcatShow" @click="concat">列合并</div>-->
    <el-col :span="24" class="wrap-main">
      <el-col :span="24" class="toolbar">
        <el-form :inline="true" :model="filters">
          <el-form-item label="工号:">
            <el-input v-model="filters.number" placeholder="请输入工号"></el-input>
          </el-form-item>
          <el-form-item label="姓名:" class="ml">
            <el-input v-model="filters.name" placeholder="请输入姓名"></el-input>
          </el-form-item>
          <el-form-item label="职位:" class="ml">
            <el-select v-model="filters.position" placeholder="请选择职位">
              <el-option value="支行行长"></el-option>
              <el-option value="客户经理"></el-option>              
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="add">新增</el-button>
          </el-form-item>
        </el-form>      
      </el-col>
      <el-col :span="4" class="table-func">
        <el-button
          size="small"
          @click="toggleSelection(tableData)">反选</el-button>
        <el-button
          size="small"
          type="danger"
          @click="clearSelection()">清空</el-button>
      </el-col>  
      <el-col :span="20" class="tl">
        <el-table
          ref="multipleTable"
          :data="tableDataCurrent" 
          class="tl" 
          border
          @header-click="headerClick"
          @selection-change="handleSelectionChange"
          max-height="300">
          <el-table-column type="selection" width="55" fixed></el-table-column>
          <el-table-column type="index" width="55" fixed></el-table-column>
          <el-table-column prop="number" label="工号" width="100" sortable></el-table-column>
          <el-table-column prop="name" label="姓名" width="300"></el-table-column>
          <el-table-column prop="position" label="职位" width="300"></el-table-column>
          <el-table-column prop="transactions" label="交易额" width="300"></el-table-column>
          <el-table-column 
            prop="approvalState" 
            label="审批状态" 
            width="300px" 
            :filters="[{ text:'未审批',value:'未审批' },{ text:'已审批',value:'已审批' }]"
            :filter-method="filterTag">
          </el-table-column>
          <el-table-column prop="transactionDate" label="交易时间" width="300px"></el-table-column>                                        
        </el-table>
      </el-col>
    </el-col>
    <el-col :span="24" class="tr">
      <div class="currentBox">当前条数<span class="currentIndex">{{currentIndex}}</span>条</div>
      <el-pagination
        class="pagination"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="pageSizes"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="15">
      </el-pagination> 
    </el-col>
    <!--<div class="block">-->
      
    <!--</div>-->
    <el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
      <formConfirm :ruleForm="ruleForm" @submit="submit"></formConfirm>
    </el-dialog>
  </el-row>
</template>

<script>
import expandData from './expandData.vue'
import formConfirm from './formConfirm.vue'
export default{
    components: {
      expandData,
      formConfirm
    },
    data(){
      return {
        // isConcatShow: false,
        pageSizes: [10, 20, 30, 40],
        pageSize: 10,
        currentPage: 1,
        currentIndex: '',
        ruleForm: {
          userName: '',
          passWord: '',
          passWordConfirm: '',
          email: '',
          phoneNumber: ''
        },
        filters: {
          number: '',
          name: '',
          position: ''
        },
        addForm: {
          number:'',
          name: '',
          position: ''
        },
        addFormVisible: false,
        tableData: [{
          number: '1',
          name: '张三',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/23'
        }, {
          number: '2',
          name: '李四',
          position: '支行行长',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/24'
        }, {
          number: '3',
          name: '王五',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/20'
        }, {
          number: '4',
          name: '马六',
          position: '客户经理',
          transactions: 100,
          approvalState: '未审批',
          transactionDate: '17/06/12'
        }, {
          number: '5',
          name: '赵七',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/06/12'
        }, {
          number: '6',
          name: '张三',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/23'
        }, {
          number: '7',
          name: '李四',
          position: '支行行长',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/24'
        }, {
          number: '8',
          name: '王五',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/20'
        }, {
          number: '9',
          name: '马六',
          position: '客户经理',
          transactions: 100,
          approvalState: '未审批',
          transactionDate: '17/06/12'
        }, {
          number: '10',
          name: '赵七',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/06/12'
        }, {
          number: '11',
          name: '张三',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/23'
        }, {
          number: '12',
          name: '李四',
          position: '支行行长',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/24'
        }, {
          number: '13',
          name: '王五',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/05/20'
        }, {
          number: '14',
          name: '马六',
          position: '客户经理',
          transactions: 100,
          approvalState: '未审批',
          transactionDate: '17/06/12'
        }, {
          number: '15',
          name: '赵七',
          position: '客户经理',
          transactions: 100,
          approvalState: '已审批',
          transactionDate: '17/06/12'
        }],
        tableDataCurrent: []
      }
    },
    mounted:function(){
      this.showData ()
    },
    methods:{
      add(){
        for (let k in this.ruleForm) {
          this.ruleForm[k] = ''
        }
        this.addFormVisible = true; 
      },
      search(){
        let i = 0;
        this.tableDataCurrent = [];
        if(this.filters.number == '' && this.filters.name == '' && this.filters.position == ''){
            this.tableDataCurrent = this.tableData;
        }else{
          for(i = 0;i <= this.tableData.length-1;i++){
            if(this.tableData[i].number == this.filters.number){
              this.tableDataCurrent.push(this.tableData[i]);
              continue;
            }
            if(this.tableData[i].name == this.filters.name){
              this.tableDataCurrent.push(this.tableData[i]);
              continue;
            }
            if(this.tableData[i].position == this.filters.position){
              this.tableDataCurrent.push(this.tableData[i]);
              continue;
            }
          }
        }
        this.filters.number = '';
        this.filters.name = '';
        this.filters.position = '';    
      },
      submit(){
        // let para = {};
        // para.number = this.addForm.number;
        // para.name = this.addForm.name;
        // para.position = this.addForm.position;
        // this.tableData.push(para);
        this.addFormVisible = false;
      },
      filterTag(value, row) {
        return row.approvalState === value;
      },
      toggleSelection(tableData) {
        tableData.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      },
      clearSelection() {
        this.$refs.multipleTable.clearSelection()
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleSizeChange(val) {
        this.pageSize = val
        this.showData ()
      },
      handleCurrentChange(val) {
        this.currentPage = val
        this.showData ()
      },
      showData () {
        let para = []
        let dataStart = this.pageSize * (this.currentPage - 1)
        let dataEnd = this.pageSize * this.currentPage
        for (let i = dataStart; i < dataEnd; i++) {
          if(this.tableData[i] !== undefined) {
            para.push(this.tableData[i])
          } else {
            this.tableDataCurrent = para
            return false
          }
        }
        this.tableDataCurrent = para
      },
      calCurrentIndex () {
        let dataStart = this.pageSize * (this.currentPage - 1)
        let dataEnd = this.pageSize * this.currentPage
        for (let i = dataStart; i < dataEnd; i++) {
          if(this.tableData[i] == undefined) {
            dataEnd = i
            break
          }
        }
        this.currentIndex = dataStart + 1 + '/' + dataEnd
      },
      headerClick ( column, event ) {
        console.log(column, event)
        this.isConcatShow = true
        // this.$parent.innerHtml = '<div class="concat" @click="concat">列合并</div>' 
        let concatBox = this.$el.querySelector('.concat')
        // console.log(concatBox)
        // console.log(this.$parent)
        concatBox.style.left = event.pageX - 207 + 'px'
        concatBox.style.top = event.pageY - 80 + 'px'
        // console.log(this.$el.querySelector('.wrap'))
        this.$emit('headerClick')
      }
      // concat () {
      //   this.isConcatShow = false
      // }
    },
    updated: function () {
      this.calCurrentIndex()
      // this.$el.querySelector('.el-table__fixed')
      if(this.$el.querySelector('.el-table__fixed-footer-wrapper') !== null) {
        let el = this.$el.querySelector('.el-table__fixed-footer-wrapper')
        console.log(el)
        el.parentNode.removeChild(el)
      }
    }
}
</script>
<style>
  .wrap {
    margin-top: 20px;
  }
  .wrap .wrap-main .table-func {
    /*width: 200px;*/
    border: 1px solid #ccc;
    height: 300px;
    /*background: red;*/
    /*display: inline-block;*/
  }
  .tl {
    text-align: left;
  }
  .tr {
    text-align: right;
  }
  .tc {
    text-align: center;
  }
  .ml {
      margin-left: 10px;
  }
  .pagination {
    float: right;
    /*display: inline-block;*/
  }
  .currentBox {
    height: 28px;
    line-height: 28px;
    font-size: 13px;
    /*display: inline-block;*/
    float: right;
    padding: 2px 5px;
  }
  .concat {
    position: absolute;
    text-align: center;
    height: 28px;
    width: 60px;
    line-height: 28px;
    background-color: #ccc;
    font-size: 13px;
    z-index: 9900;
    cursor: pointer;
  }
</style>



