<template>
  <el-row class="wrap">
    <el-col :span="24" class="wrap-main">
      <el-col class="toolbar" :span="16" :push="4">
        <el-form :inline="true" :model="filters" class="tl form" label-width="80px">
          <el-form-item label="机构名称:">
            <el-select v-model="filters.institution">
              <el-option value="北京支行"></el-option>
              <el-option value="长沙支行"></el-option>              
            </el-select>
          </el-form-item>
          <br>
          <el-form-item label="报表日期:">
            <el-date-picker v-model="filters.date" type="date" placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="币种:">
            <el-select v-model="filters.currency" multiple>
              <el-option value="人名币"></el-option>
              <el-option value="美元"></el-option>
              <el-option value="欧元"></el-option>
              <el-option value="英镑"></el-option>              
            </el-select>
          </el-form-item>
          <br>
          <el-form-item label="机构级别:">
            <el-checkbox-group v-model="filters.checkList">
              <el-checkbox label="支行"></el-checkbox>
              <el-checkbox label="二级分行"></el-checkbox>
              <el-checkbox label="分行"></el-checkbox>
              <el-checkbox label="大中型客户团队"></el-checkbox>
              <el-checkbox label="小企业金融中心"></el-checkbox>
              <el-checkbox label="小企业专职团队"></el-checkbox>              
            </el-checkbox-group>
          </el-form-item>
          <br>
          <el-form-item label="单位:">
            <el-select v-model="filters.unit">
              <el-option value="元"></el-option>
              <el-option value="万元"></el-option>              
            </el-select>
          </el-form-item>
          <br>
          <el-form-item>
            <el-button type="primary" @click="search">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="reset">重置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-col>
  </el-row>
</template>
<script>
export default {
  data(){
    return {
      filters:{
        institution:'',
        date:'',
        currency:'',
        checkList:[],
        unit:''
      }
    }
  },
  methods:{
    search(){
      console.log("查询完毕");
    },
    reset(){
      let filters = this.filters;
      for( let k in filters){
        if( k == 'checkList'){
          filters[k] = [];
        }else {
          filters[k] = '';
        }       
      }
    }
  }
}
</script>
<style>
  .tl {
    text-align: left;
  }
  .toolbar {
    margin-top: 40px;
  }
</style>




