<template>
  <el-row>
    <el-col :span="24" class="top-banner">
      <a href="#" class="logo">logo</a>
      <span class="user">zyq</span>
    </el-col>
    <el-col :span="24" class="main clearfix">
      <el-menu class="aside-nav" default-active="1" @select="handleOpen">
        <template v-for="item in $router.options.routes[1].children">
          <!--<el-submenu class="nav-item">{{item.name}}</el-submenu>-->
          <el-menu-item class="nav-item" :index="item.path" @click="$router.push(item.path)">{{item.name}}</el-menu-item>
        </template>
      </el-menu>
      <div class="main-content clearfix">
        <router-view></router-view>
      </div>
    </el-col>
  </el-row>
</template>
<script>


export default {
  data(){
      return {
        isConcatShow: true
      }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    headerClick () {
      this.isConcatShow = true
    },
    concat (e) {
      console.log('2',e)
    }
  }
}
</script>
<style>
  .top-banner {
    height: 60px;
    line-height: 60px;
    background-color:aqua;
  }
  .top-banner .logo {
    float: left;
    height: 100%;
    width: 80px;
    background-color: gray;
    margin-left: 10px;
    color:black;
    text-decoration: none;
  }
  .top-banner .user {
    float: right;
    height: 100%;
    width: 80px;
    background-color: gray;
    margin-right: 10px;
    cursor: pointer;
  }
  .main .aside-nav {
    float:left;
    width: 15%;
  }
  .main .aside-nav .nav-item {
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    cursor: pointer;
  }
  .main .main-content{
    float:left;
    /*background-color:red;*/
    width: 85%;
  }
  .clearfix::after {
    content: ".";
    clear: both;
    display: block;
    overflow: hidden;
    font-size: 0;
    height: 0;
  }

  .clearfix {
    zoom: 1;
  }
  .concat {
    position: absolute;
    text-align: center;
    height: 28px;
    width: 60px;
    line-height: 28px;
    background-color: #ccc;
    font-size: 13px;
    z-index: 9900;
    cursor: pointer;
  }
</style>





