<template>
  <table>
    <thead>
      <tr>
        <template v-for="(item,key) in rowHead">
          <th @dblclick="rowFuncClick" @click="rowOrder" :index="key">
            {{item}} 
            <span class="arrow"></span>
            <div class="rowfunc">
              <div @click="rowOrderToggle">列排序</div>
              <div>列筛选</div>
              <div>列合计</div>
            </div>
          </th>
        </template>
      </tr>
    </thead>
    <tbody :class="{serialNum: isIndex}">
      <template v-for="(groupList,index) in group">
        <div @click="showgroup" class="grouplist" :index="index">{{groupList.groupName}} <span class="arrow" :class="{ asc:groupList.asc,dsc:!groupList.asc }"></span></div>
        <template>
          <tr v-if="!groupList.asc" v-for="item in groupList.lists">
            <template v-for="item in item">
              <td>{{item}}</td>
            </template>
          </tr>
        </template>
      </template>
    </tbody>
    <tfoot>
      <tr>
        <td>合计</td>
        <td></td>
        <td></td>
      </tr>
    </tfoot>
  </table>
</template>
<script>
import data from './util/mock'
export default {
  data () {
    return {
      isIndex: true,
      th: '',
      rowHead: {
        id: '工号',
        name: '姓名',
        profit: '交易额(万元)',
        area: '地区'
      },
      group: [
        {
          groupName: '机构分组',
          asc: true,
          lists:[]
        },
        {
          groupName: '部门分组',
          asc: true,
          lists:[]
        }
      ] 
    }
  },
  methods: {
    showgroup (e) { 
      let index = e.currentTarget.getAttribute('index')
      this.group[index].asc = !this.group[index].asc
    },
    rowFuncClick (e) {
      console.log(e)
      let target = e.currentTarget
      target.lastElementChild.style.visibility = 'visible'
      this.th = e.currentTarget
      target.lastElementChild.style.left = e.offsetX + 'px'
      target.lastElementChild.style.top = e.offsetY + 'px'
    },
    rowOrderToggle (e) {
      e.stopPropagation()
      console.log(e)
      e.currentTarget.parentNode.style.visibility = 'hidden'
      let isClass = this.th.firstElementChild.classList.contains('asc') ||
                    this.th.firstElementChild.classList.contains('dsc')
      if( isClass === true ) {
        this.th.firstElementChild.classList.remove('asc','dsc')
        e.currentTarget.innerHTML = '列排序'        
      } else {
        this.th.firstElementChild.classList.add('asc')
        e.currentTarget.innerHTML = '取消排序'
        let index = this.th.getAttribute('index')
        for(let i = 0;i < this.group.length;i++) {
          this.group[i].lists.sort(function(a,b){
            return a[index] - b[index]
          })
        }
      }
    },
    rowOrder (e) {
      let target = e.currentTarget
      let isClass = target.firstElementChild.classList.contains('asc') ||
                    target.firstElementChild.classList.contains('dsc')
      if (isClass === true) {
        target.firstElementChild.classList.toggle('asc')
        target.firstElementChild.classList.toggle('dsc')
        let index = target.getAttribute('index')
        if(target.firstElementChild.classList.contains('asc')){
          for(let i = 0;i < this.group.length;i++) {
            this.group[i].lists.sort(function(a,b){
              return a[index] - b[index]
            })
          }
        } else {
          for(let i = 0;i < this.group.length;i++) {
            this.group[i].lists.sort(function(a,b){
              return b[index] - a[index]
            })
          }
        } 
      }
    }
  },
  mounted: function () {
    this.$http.post("http://helloworld.cn").then(
      (res) => {
        console.log(res)
        this.group = res.data.data
        console.log(this.group)
      },
      (fileData) => {console.log(fileData)}
    )
    if(this.$el.querySelector('.serialNum')){
      console.log('1')
    }
  }
}
</script>

<style scoped>
body {
  font-family: Helvetica Neue, Arial, sans-serif;
  font-size: 14px;
  color: #444;
}

table {
  margin-top: 100px;
  /*margin-left: 200px;*/
  float: right;
  width: 80%;
  overflow: scroll;
  border: 2px solid #42b983;
  border-radius: 3px;
  background-color: #fff;
  position: relative;
}
.rowfunc {
  position: absolute;
  background: #e0e0e0;
  font-family: Helvetica Neue, Arial, sans-serif;
  font-size: 13px;
  color: #444;
  border-radius: 2px;
  overflow: hidden;
  visibility: hidden;
  z-index: 9000;
}
.rowfunc > div {
  width: 80px;
  height: 28px;
  line-height: 28px;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
}
.rowfunc > div:hover {
  background: #888;
}
thead {
  display: block;
}
tbody {
  text-align: left;
  display: block;
  height: 300px;
  overflow-y: scroll;
}
tbody .grouplist {
  /*width: 876px;*/
  width: 100%;
  height: 20px;
  line-height: 20px;
  font-size: 13px;
  background-color: #e0e0e0;
  padding-left: 10px;
  margin-top: 5px;
  border-radius: 2px;
  cursor: pointer;
}
tfoot {
  display: block;
}
th {
  background-color: #42b983;
  color: rgba(255,255,255,0.66);
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
}

td {
  background-color: #f9f9f9;
}

th, td {
  min-width: 120px;
  max-width: 120px;
  padding: 10px 20px;
  text-align: center;
}

th.active {
  color: #fff;
}

th.active .arrow {
  opacity: 1;
}

.arrow {
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 0;
  margin-left: 5px;
  opacity: 0.66;
}

.arrow.asc {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid brown;
}

.arrow.dsc {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid brown;
}
</style>




